/* === GLOBAL STYLES === */

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  background: white;
  color: #2d0c30;
  font-family: "Barlow", sans-serif;
  font-size: 2rem;
}

.footer {
  width: 100%;
  text-align: center;
  /* position: fixed; */
  padding: 30px 0;
  background-color: #2d0c30;
  color: whitesmoke;
  /* bottom: 0; */
}

/* --- Typography --- */

h1,
h2,
h3 {
  font-family: "Montserrat", sans-serif;
  /* padding: 20px 0; */
  width: 80%;
  font-family: "Barlow", sans-serif;
}

p,
a,
label,
input,
button {
  font-size: 2rem;
}

label,
input {
  color: #2d0c30;
}

a {
  text-decoration: none;
}

button,
.button {
  background: #e27a05;
  border: 2px solid transparent;
  border-radius: 5px;
  box-shadow: 0 0 7px rgba(45, 12, 48, 0.3.5);
  color: whitesmoke;
  cursor: pointer;
  display: inline-block;
  font-size: 2rem;
  margin: 15px 0;
  padding: 10px 20px;
  transition: transform 200ms;
}

button:hover {
  border-color: #2d0c30;
}

button:focus,
button:focus-within,
.button:hover,
.button:focus,
.button:focus-within {
  border: 4px solid #2d0c30;
  outline: none;
}

a:hover,
a:focus {
  font-weight: bold;
}

/* --- Layout ---*/

/* Header and main are styled seperately here for layout concerns */

.header h1 {
  margin: 0;
}

.pageContainer,
.homePage .copyContainer {
  margin: 0 auto;
  max-width: 1100px;
  width: 90%;
}

/* === HEADER COMPONENT === */

header {
  background: #e27a05;
  color: whitesmoke;
}

.header {
  align-items: center;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  max-width: 1400px;
  margin: 0 auto;
  width: 90%;
}

header a {
  color: whitesmoke;
}

/* === HOME PAGE COMPONENT === */

.homePage {
  background: #eae100;
  display: block;
  height: auto;
}

.homePage,
.homePage h2 {
  font-size: 4rem;
}

.homePage h3 {
  font-size: calc(15px + (40 - 15) * ((100vw - 300px) / (1600 - 300)));
}

.homePage .copyWrapper {
  width: 75%;
}

.homePage .right {
  float: right;
  width: 150px;
}

.styleBlock {
  align-items: center;
  display: flex;
  height: 28.3333vh;
  min-height: 150px;
  padding: 1em 0;
}

.styleBlock.block1 {
  background: #f5f5f5;
}

.styleBlock.block2 {
  background: #2d0c30;
  color: #f5f5f5;
}

.copyWrapper p {
  width: 75%;
  margin-bottom: 1.5em;
}

.styleBlock.block3 {
  background: #eae100;
}

.toCreatePoll {
  background: #f5f5f5;
  border: 4px solid #2d0c30;
  border-radius: 10px;
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
  border-right: none;
  box-shadow: 0 0 15px rgba(45, 12, 48, 0.4);
  padding: 1em;
  position: fixed;
  /* slide in effect of side sticker part1*/
  right: -20em;
  top: 30%;
  width: 33%;
}

/* slide in effect of side sticker part2*/

main:hover .toCreatePoll {
  right: 0;
  transition-duration: 0.7s;
}

.toCreatePoll p {
  padding: 5px 0;
}

/* ============================================ *

/* === MEDIA queries home page  === */

@media (max-width: 740px) {
  .homePage .copyWrapper {
    width: 100%;
  }
  .styleBlock {
    height: auto;
  }
  .toCreatePoll {
    /* sticker moves down to the page */
    position: relative;
    right: 0;
    width: 100%;
    border: 4px solid #2d0c30;
    border-radius: 10px;
    border-bottom-right-radius: 0;
    border-bottom-left-radius: 0;
  }
}

@media (max-height: 600px) {
  .styleBlock {
    height: auto;
  }
}
