/* === !!! Remove before merge back with index.css file === */

/* === GLOBAL STYLES === */

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

/* --- Typography --- */

h1,
h2,
h3 {
  font-family: "Montserrat", sans-serif;
  padding: 20px 0;
}

label,
input {
  color: #2d0c30;
}

a {
  text-decoration: none;
}

/* === POLL ANALYTICS COMPONENT === */

.analytics {
  background: white;
  color: #2d0c30;
  font-size: 2rem;
}

.analytics h2 {
  padding: 50px 0 25px 0;
}

.analyticsInfo {
  background: whitesmoke;
  border-radius: 20px;
  margin: 30px auto;
  padding: 25px 50px;
  width: 90%;
  border: 7px solid #2d0c30;
  border-radius: 15px;
}

.analyticsInfo .copyWrapper {
  width: 75%;
}

.analyticsInfo h2,
.analyticsInfo p {
  width: 100%;
}

.analyticsInfo h2 {
  width: 25%;
}

.analytics p {
  font-family: "Barlow", sans-serif;
  margin: 10px;
  line-height: 1.6;
  font-size: calc(15px + (25 - 15) * ((100vw - 300px) / (1600 - 300)));
}

.pollQuestion {
  font-size: 3.5rem;
}

.pollCounters {
  display: flex;
  font-family: "Montserrat", sans-serif;
  justify-content: space-around;
  margin: auto;
  flex-wrap: wrap;
  justify-content: center;
}

.count {
  border-radius: 5px;
  flex-basis: 32%;
  text-align: center;
  min-width: 100px;
  margin: 5px 5px;
}

.count p {
  line-height: normal;
  width: auto;
}

.countA {
  background: #eae100;
  color: #2d0c30;
}

.countB {
  background: #2d0c30;
  color: #f5f5f5;
}

.countTotal {
  background: #e27a05;
  color: #f5f5f5;
}

p.countNum {
  font-family: "Montserrat", sans-serif;
  font-size: calc(25px + (70 - 25) * ((100vw - 300px) / (1600 - 300)));
}

/* styles for users unique poll link on analytics page */
.analyticsInfo .usersPollLink {
  font-size: 1.8rem;
  border: 2px solid #2d0c30;
  padding: 7px 15px;
  border-radius: 10px;
  background-color: rgb(228, 225, 225);
  width: fit-content;
}

@media (max-width: 600px) {
  .analyticsInfo .usersPollLink {
    font-size: 1.4rem;
    word-wrap: break-word;
  }
  .analyticsInfo {
    padding: 10px;
    width: 98%;
  }
}

@media (max-width:400px) {
  .analyticsInfo .usersPollLink {
    font-size: 1.1rem;
  }
}

